.header {
  background: url("../img/svg/logo jeep.svg") center no-repeat;
  background-size: contain;
  height: 55vh;
  width: 100%;
}

.header__title {
  font-size: px-to-rem(32px);
  line-height: px-to-rem(38px);
  font-weight: bold;
  text-align: center;
  margin-top: 3em;
  margin-bottom: 1em;
  color: $active;
}

.header__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.item {
  list-style: none;
  display: block;
  font-size: px-to-rem(24px);
  line-height: px-to-rem(28px);
  border: 1px solid $active;
  margin-bottom: 1em;
}

.sto {
  font-size: px-to-rem(48px);
  line-height: px-to-rem(56px);
}

.item_icon {
  height: 2em;
}

.item_text {
  display: block;
  margin: 0 auto;
}


@media (min-width: 820px) {

  .header__title {
    font-size: px-to-rem(48px);
    line-height: px-to-rem(56px);
  }

  .header__list {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
  }

  .item {
    flex-basis: 25em;
    margin: 1.5em;
  }

  .item_icon {
    margin-right: 1em;
    margin-left: 1em;
  }

}

@media (min-width: 1024px) {

  .item {
    flex-basis: 17em;
    margin: 1.5em;
  }

}


@media (min-width: 1200px) {
  .header__title {
    font-size: px-to-rem(64px);
    line-height: px-to-rem(72px);
  }

}

@media (min-width: 1400px) {
  .item {
    flex-basis: 23em;
  }
}


@media (min-width: 1500px) {
  .header {
    .header__title {
      margin-top: 2em;
      margin-bottom: .5em;
    }

    .item {
      flex-basis: 25em;
    }
  }

}

@media (min-width: 1850px) {
  .header__title {
    font-size: px-to-rem(72px);
    line-height: px-to-rem(84px);
  }

  .item {
    flex-basis: 30em;
  }
}