.fixed {
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  color: $white;
  box-sizing: border-box;
  width: 100%;
  border-top: 3px solid $white;
  border-bottom: 3px solid $white;
  padding-top: 4px;
  padding-bottom: 4px;
  max-height: 100px;
  color: $background;
  background-color: $background;
  transition: all 0.4s ease;

  a:hover, :focus {
    background-color: inherit;
  }
}

.fixed__top{
  top: 0px;
  transition: all 0.4s ease;
}

.fixed__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;

}

.fixed__phone {
  a {
    display: block;
    text-decoration: none;
  }
}

.fixed__logo {
  a {
    img {
      height: 2.2rem;
      width: auto;
    }
  }
}

.fixed__menu {
  display: none;
}

.fixed__menu_hamburger, .fixed__hamburger {
  flex-basis: 32px;
  flex-shrink: 0;
  cursor: pointer;

  span {
    display: block;
    background-color: $white;
    height: 3px;
    width: 100%;
  }

  .humburger_top {
    width: 22px;
    margin-left: auto;
    margin-bottom: 7px;
  }

  .humburger_middle {
    width: 28px;
    margin-left: auto;
    margin-bottom: 7px;
  }
}

.fixed__search {
  display: none;
}

@media (min-width: 640px ) {
  .fixed__wrap {
    padding: 0;
  }

  .fixed__phone {
    display: none;
  }

 .fixed__menu, .fixed__search {
    display: flex;
    order: 0;
    height: 2.5rem;
    flex-basis: 26%;
    align-items: center;
    justify-content: flex-end;
    background: linear-gradient( 130deg, $white 84%, transparent 1%);
    font-size: px-to-rem(24px);
    font-weight: bold;
    font-style: italic;


    .menu_text {
      display: block;
      margin-right: 5%;
      width: 100%;
      text-align: right;
      border-bottom: 3px solid $background;
    }


    .fixed__menu_hamburger {
      margin-right: 20%;
     span {
       background-color: $background;
     }
    }
  }
  .fixed__hamburger {
    display: none;
  }

  .fixed__logo {
    a {
      img {
        height: 3rem;
      }
    }
  }

  .fixed__search {
    background: linear-gradient( 310deg, $white 83%, transparent 1%);
    order: 2;
    height: 2.5rem;

    .fixed__search_underline {
      margin-left: 15%;
      margin-right: 5%;
      width: 100%;
      text-align: right;
      border-bottom: 3px solid $background;

      .search_text {
        opacity: 50%;
      }
    }

    .search_icon {
      height: 80%;
      margin-right: 5%;
    }
  }
}

@media (min-width: 1024px ) {
  .fixed__menu {
    height: 4rem;
  }

  .fixed__logo {
    a {
      img {
        height: 4.5rem;
        width: auto;
      }
    }
  }

  .fixed__search {
    height: 4rem;
  }
}