@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $white;
	background: $background;
}

body {
	font-family: 'Roboto', 'Arial', sans-serif;
	font-style: italic;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.container {
	@include min-max(320px, 100%, 0);
}

//my Style
.wrap {
	//@include min-max(320px, 1140px, 15px);
	@include min-max(320px, 2048px, 15px);

	position: relative;
	z-index: 1;

	&::before {
		background: $background;
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: -1;
		opacity: 0.9;
	}
}

.uppercase {
	text-transform: uppercase;
}

a {
	text-decoration: none;
	color: inherit;
	box-sizing: border-box;

	&:hover, &:focus {
		background-color: $active;
		outline: none;
	}
}

.btn {
	display: flex;
	box-sizing: border-box;
	padding: 1rem 2rem;
	align-items: center;
	justify-content: space-between;
	text-align: center;
}

